import './App.css';
import { Controller, Scene } from 'react-scrollmagic';
import ExampleDotMatrix from './components/ExampleDotMatrix';
import ShootingsMap from './components/ShootingsMap';
import ParentSize from '@visx/responsive/lib/components/ParentSize';
import VoronoiDiagram from './components/VoronoiDiagram';
import HomicideRateBarChart from './components/HomicideRateBarChart.jsx';
import useStore from './store/index';
import TextTrigger from './components/TextTrigger';
import ShootingsHomicidesIncarcerationChart from './components/ShootingsHomicidesIncarcerationChart';
import SocialFactorsMap from './components/SocialFactorsMap';
import covidPlaceholder from './assets/images/covid-placeholder.png';
import HeaderText from './components/HeaderText';

import SlideA from './components/covid-moment/SlideA';
import SlideB from './components/covid-moment/SlideB';
import SlideC from './components/covid-moment/SlideC';
import SlideD from './components/covid-moment/SlideD';

import { useRef } from 'react';



function App() {
  // for navigating the map timelines
  const ref1993 = useRef(null);
  const ref2020 = useRef(null);
  const ref2021 = useRef(null);

  const refDisconnectedYouth = useRef(null);
  const refCommmunityTrust = useRef(null);
  const refUnemployment = useRef(null);
  const refGraduationRate = useRef(null);
  const refPovertyRate = useRef(null);

  const getRefPosition = (duration, index, total) => `calc(-99vh + ${(duration * (index/total))}px)`; 

  const headerTexts = useStore((store) => store.headerTextArray);
  return (
    <div>
      <div className='hidden md:grid grid-cols-12 fixed w-screen p-8 md:p-16'>
        <div className="col-span-3">
          <blockquote className="relative block w-full h-screen"><HeaderText texts={headerTexts} /></blockquote>
        </div>
      </div>
      <Controller>
        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <div className="space-y-8">
            <p>Safety depends on every New Yorker and every neighborhood.</p>
            <p>History has shown that something more than the force of the criminal justice system keeps New York City safe.</p>
          </div>
        </div>

        <Scene duration="4500" triggerHook="onLeave" pin>
          {
            (progress, event) => (
              <div className="w-screen h-screen block grid grid-cols-12 p-8 md:p-16">
                <TextTrigger triggerPoint={0.001} progress={progress} event={event} text="Safety depends on every New Yorker and every neighborhood." />
                <TextTrigger triggerPoint={0.1} progress={progress} event={event} text="History has shown that something more than the force of the criminal justice system keeps New York City safe." />
                <div className="col-span-full md:col-span-7 md:col-start-5 bg-white">
                  <ParentSize>
                    {
                      ({width, height}) => <VoronoiDiagram progress={progress} width={width} height={height} />
                    }
                  </ParentSize>
                </div>
              </div>
            )
          }
        </Scene>

        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <p>For 30 years, New Yorkers have committed fewer murders and shootings, while fewer people were jailed.</p>
        </div>
        
        <Scene duration={1800} pin triggerHook="onLeave">
          {
            (progress, event) => (
              <div className='w-screen h-screen p-8 md:p-16 grid grid-cols-12 grid-rows-header'>
                <TextTrigger progress={progress} event={event} text="For 30 years, New Yorkers have committed fewer murders and shootings, while fewer people were jailed." />
                  <h3 className="col-span-full md:col-start-5 md:col-span-7 bg-white">NYC homicides, shootings and incarceration, 1993-2021</h3>
                  <div className='col-span-full md:col-span-7 md:col-start-5 row-start-2 bg-white'>
                      <ParentSize>
                        {
                          ({width, height}) => <ShootingsHomicidesIncarcerationChart prescroll={0.2} progress={progress} width={width} height={height} />
                        }
                      </ParentSize>
                  </div>
                  <div className="md:col-start-5 md:col-span-4 row-start-3 text-gray-500 text-xs pt-4">
                    Source:  NYPD Compstat
                  </div>
              </div>
            )
          }
        </Scene>

        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <p>Today, New York City has the lowest murder rate of the top 5 largest cities in the US.</p>
        </div>
        
        <Scene duration={300} pin triggerHook="onLeave">
          {
            (progress, event) => {
              return (
                <div className=" w-screen h-screen p-8 md:p-16 grid grid-cols-12 grid-rows-header">
                  <TextTrigger progress={progress} event={event} text="Today, New York City has the lowest murder rate of the top 5 largest cities in the US." />
                  <TextTrigger progress={progress} event={event} triggerPoint={0.99} text="" />
                  <h3 className="col-span-full md:col-span-7 md:col-start-5 justify-self-start bg-white">Homicide rates of the ten largest US cities, 2021</h3>
                  <div className="col-span-full md:col-span-7 md:col-start-5 row-start-2 bg-white">
                    <ParentSize>
                      {
                        ({width, height}) => <HomicideRateBarChart progress={progress} width={width} height={height} />
                      }
                    </ParentSize>
                  </div>
                  <div className="md:col-start-5 md:col-span-4 row-start-3 text-gray-500 text-xs pt-4">
                    Source: Population: 2020 census. Murder rates found in local news media; San Diego rate is estimated due to lack of official murder count.
                  </div>
                </div>
              );
            }
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          {
            (progress) => {
              return (
                <div>
                    <SlideA />
                </div>
            )}
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          {
            (progress) => {
              return (
                <div>
                    <SlideB />
                </div>
            )}
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          {
            (progress) => {
              return (
                <div>
                    <SlideC />
                </div>
            )}
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          {
            (progress) => {
              return (
                <div>
                    <SlideD />
                </div>
            )}
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          <div className="h-screen w-screen flex items-center content-center justify-center">
            <div className='basis-1/2 font-serif text-2xl text-center px-16 py-24 border-32 border-periwinkle relative'>
              {/*<div className="absolute top-0 left-0 right-0 bottom-0">*/}
                <div className="absolute left-1/2 -top-32px -translate-x-1/2 h-8 w-8 bg-white"></div>
                <div className="absolute left-1/2 -bottom-32px -translate-x-1/2 h-8 w-8 bg-white"></div>
              {/*</div>*/}
              <p className="leading-7">Some things changed, some things remained the same. Most importantly, NYC is still the safest city, but not everyone is safe everywhere.</p>
            </div>
          </div>
        </Scene>

        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <p>Shooting victims are overwhelmingly young, Black men.</p>
        </div>
        
        <Scene duration={1800} triggerHook="onLeave" pin>
          {
            (progress, event) => (
              <div className='h-screen w-screen grid grid-cols-12 grid-rows-header p-8 md:p-16'>
                <TextTrigger progress={progress} event={event} text="Shooting victims are overwhelmingly young, Black men."></TextTrigger>
                <h3 className="col-span-full md:col-span-8 md:col-start-5 grid-start-1 grid-span-1 bg-white">NYC shooting victimization rates, ages 18-24 YTD 2021<br />(per 100,000 residents)</h3>
                <div className="col-span-full md:col-span-8 md:col-start-5 bg-white">
                  <div className='md:flex mt-4 md:flex-row'><ExampleDotMatrix progress={progress} /></div>
                </div>
                <div className="md:col-start-5 md:col-span-4 row-start-3 text-gray-500 text-xs pt-4">
                  Source: lorem ipsum.
                </div>
              </div>
            )
          }
        </Scene>

        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <p>The city's poorest neighborhoods, home almost entirely to Black and Latino New Yorkers bear the brunt of violence. 2/3 of the city's shootings occur in Brooklyn and the Bronx, and that's been consistent over time.</p>
        </div>
        
        <Scene duration={1200} pin triggerHook="onLeave" pinSettings={{pushFollowers: false}}>
          {
            (progress, event) => (
              <div className="h-screen w-screen grid grid-cols-12 grid-rows-header p-8 md:p-16 ">
                <TextTrigger progress={progress} event={event} text="The city's poorest neighborhoods, home almost entirely to Black and Latino New Yorkers bear the brunt of violence. 2/3 of the city's shootings occur in Brooklyn and the Bronx, and that's been consistent over time." />
                <h3 className="col-span-full md:col-span-8 md:col-start-5 bg-white">Number of shootings by precinct, 1993, 2019, and 2020</h3>
                <div className="col-span-full md:col-span-8 md:col-start-5 pt-4 bg-white">
                  <ShootingsMap yearRefs={{'1993': ref1993, '2020': ref2020, '2021': ref2021}} progress={progress} />
                </div>
                <div className="md:col-start-5 md:col-span-4 row-start-3 text-gray-500 text-xs pt-4">
                  Source: lorem ipsum.
                </div>
              </div>
            )
          }
        </Scene>

        <div style={{height: '1200px', position: 'relative', zIndex: -1}}>
          <a ref={ref1993} style={{position: 'absolute', top: getRefPosition(1200, 0, 3)}} id="map1993" name="map1993"></a>
          <a ref={ref2020} style={{position: 'absolute', top: getRefPosition(1200, 1, 3)}} id="map2020" name="map2020"></a>
          <a ref={ref2021} style={{position: 'absolute', top: getRefPosition(1200, 2, 3)}} id="map2021" name="map2021"></a>
        </div>

        <div className="h-screen w-screen p-8 flex items-center justify-center text-2xl font-serif text-center md:hidden">
          <p>And the conditions that incubate violence...</p>
        </div>

        <Scene duration={1200} pin pinSettings={{pushFollowers: false}} triggerHook="onLeave">
          {
            (progress, event) => (
              <div className="h-screen w-screen grid grid-cols-12 grid-rows-header p-8 md:p-16">
                <TextTrigger progress={progress} event={event} text="And the conditions that incubate violence..." />
                <h3 className="col-span-full md:col-span-8 md:col-start-5 bg-white">Title of viz goes here</h3>
                <div className="col-span-full md:col-span-8 md:col-start-5 pt-4 bg-white">
                  <SocialFactorsMap 
                    factorRefs={{'Disconnected Youth': refDisconnectedYouth, 'Community Trust': refCommmunityTrust, 'Unemployment': refUnemployment, 'Graduation Rate': refGraduationRate, 'Poverty Rate': refPovertyRate}} 
                    progress={progress}
                  />
                </div>
                <div className="md:col-start-5 md:col-span-4 row-start-3 text-gray-500 text-xs pt-4">
                  Source: lorem ipsum.
                </div>
              </div>
            )
          }
        </Scene>

        <div style={{height: '1200px', position: 'relative', zIndex: -1}}>
          <a ref={refDisconnectedYouth} style={{position: 'absolute', top: getRefPosition(1200, 0, 5)}} id="mapDisconnectedYouth"></a>
          <a ref={refCommmunityTrust} style={{position: 'absolute', top: getRefPosition(1200, 1, 5)}} id="mapCommmunityTrust"></a>
          <a ref={refUnemployment} style={{position: 'absolute', top: getRefPosition(1200, 2, 5)}} id="mapUnemployment"></a>
          <a ref={refGraduationRate} style={{position: 'absolute', top: getRefPosition(1200, 3, 5)}} id="mapGraduationRate"></a>
          <a ref={refPovertyRate} style={{position: 'absolute', top: getRefPosition(1200, 4, 5)}} id="mapPovertyRate"></a>
        </div>

        <Scene duration={1} triggerHook="onEnter" pin>
          {
            (progress, event) => (
              <div className="h-screen w-screen flex items-center content-center justify-center">
                <TextTrigger event={event} progress={progress} text="" />
                <div className='md:basis-1/3 font-serif text-center'>
                  <p className="text-4xl mb-8">It doesn't have to be this way.</p>
                  <a className="bg-black p-4 uppercase text-sm text-white font-mono" href="#">Read issue 1 on gun violence</a>
                </div>
              </div>)
          }
        </Scene>
      </Controller>
      <div className='bg-gray-600 relative' style={{height: '200px'}}></div>
    </div>
  );
}

export default App;
