import { voronoi, VoronoiPolygon } from '@visx/voronoi';
import { Group } from '@visx/group';
import { getSeededRandom } from '@visx/mock-data';
import { useMemo } from 'react';
import {clamp, getProgressBetweenPoints} from '../lib';

const seededRandom = getSeededRandom(0.88);

const maxStrokeWidth = 30;

const defaultMargin = {
  top: 0,
  left: 0,
  right: 0,
  bottom:0,
};

const cellCount = 100;

const data = new Array(cellCount).fill(null).map(() => {
  return {
    x: seededRandom(),
    y: seededRandom(),
  }
});


/* 
 * progress positions at which various transitions occur 
 */
// text
const inflectionPointWords = 0.2
const inflectionPointWordsComplete = 0.75;
const inflectionPointWordsDisappear = 0.8;

// voronoi division
const inflectionPointVoronoiStarts = 0.0;
const inflectionPointVoronoiComplete = 0.2;

// black lines
const inflectionPointLinesStart = 0.1;
const inflectionPointLinesMax = 0.2;
const inflectionPointLinesGone = 0.75;

// semi-opaque background
const inflectionPointDarken = 0.2;
const inflectionPointLighten = 0.8;

const words = ['Community trust', 'Libraries', 'Good jobs', 'Safe/decent housing', 'Well maintained parks', 'Prospering businesses', 'Access to healthy food', 'Public transportation', 'Youth resources', 'Access to healthcare', 'Cultural activities', 'Places to gather', 'Recreational spaces'];

const colors = ['#ff7c53', '#dde44c', '#e7466d', '#cea9be', '#217ebe', '#cdcfde'];

const getFill = (index) => colors[(index % colors.length)];
const getWordBackground = (index) => colors[index % colors.length];
const getWordOpacity = (progress) => ((progress > inflectionPointWords) && (progress < inflectionPointWordsDisappear)) ? 1 : 0;
const getBackgroundOpacity = (progress) => (inflectionPointLighten > progress && progress > inflectionPointDarken) ?  0.75 - getProgressBetweenPoints(progress, inflectionPointDarken, inflectionPointLighten) * 0.75 : 0;

const getStrokeWidth = (progress) => {
  if(progress < inflectionPointLinesStart) {
    return 0;
  }
  if(inflectionPointLinesMax > progress > inflectionPointLinesStart){
    return getProgressBetweenPoints(progress, inflectionPointLinesStart, inflectionPointLinesMax) * maxStrokeWidth;
  }

  if(inflectionPointLinesGone > progress > inflectionPointLinesMax) {
    return  (1 - getProgressBetweenPoints(progress, inflectionPointLinesMax, inflectionPointLinesGone)) * maxStrokeWidth
  }
  return 0;
}

const VoronoiDiagram = ({width, height, progress, margin = defaultMargin}) => {
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;

  const dataSlice = data.slice(0, clamp(Math.round(getProgressBetweenPoints(progress, inflectionPointVoronoiStarts, inflectionPointVoronoiComplete) * data.length), 0, data.length));

  const voronoiLayout = useMemo(
    () =>
      voronoi({
        x: (d) => d.x * innerWidth,
        y: (d) => d.y * innerHeight,
        width: innerWidth,
        height: innerHeight,
      })(dataSlice),
    [innerWidth, innerHeight, dataSlice],
  );

  const polygons = voronoiLayout.polygons();

  return (
    <div style={{width: `${innerWidth}px`, height: `${height}px`, position: 'relative',}}>
      <div style={{position: 'absolute', top: 0, left: 0, bottom: 0, right: 0, background: `rgba(0, 0, 0, ${getBackgroundOpacity(progress)})`, transition: 'background 0.5s'}}></div>
      <svg width={width} height={height}>
        <Group
          top={margin.top}
          left={margin.left}
        >
          {polygons.map((polygon, i) => (
            <VoronoiPolygon
              key={`polygon-${i}`}
              polygon={polygon}
              stroke="black"
              strokeWidth={getStrokeWidth(progress)}
              fill={getFill(i)}
            />
          ))}
        </Group>
      </svg>
      {
        words.slice(0, Math.round(getProgressBetweenPoints(progress, inflectionPointWords, inflectionPointWordsComplete) * words.length)).map((word, i) => {
          const pointIndex = i * Math.round(dataSlice.length / words.length);
          const point = data[pointIndex];
          return <div 
              key={`voronoi-word-${word.split(' ').join('-')}`} 
              style={{
                position: 'absolute', 
                top: `${point.y * innerHeight}px`, 
                left: `${point.x * innerWidth}px`, 
                background: getWordBackground(i), 
                padding: '.5rem', 
                fontSize: '20px', 
                fontWeight: 'bold',
                opacity: getWordOpacity(progress),
                transition: 'opacity 0.5s',
              }}
          >
            {word}
          </div> 
        })
      }
    </div>
  );
}

export default VoronoiDiagram;