const MemoizedMap = React.memo(ShootingsMapMap);

// getters
const getShootings = (datum) => datum.hasOwnProperty('shootings') ? datum.shootings : null;
const getYear = (datum)=> datum.year;

const years = shootingsByPrecinctYear.map(getYear).reduce((acc, item) => acc.indexOf(item) === -1 ? acc.concat([item]) : acc, []);

const shootingsCounts = shootingsByPrecinctYear.map(getShootings);
const min = (data) => Math.min(...data);
const max = (data) => Math.max(...data);


const shootingsColorScale = scaleLinear({
  domain: [min(shootingsCounts), max(shootingsCounts)],
  range: ['#cea9bd', '#e7466c']
});

const ShootingsMap = ({progress, yearRefs}) => {  

  const [yearIndex, setYearIndex] = useState(0);
  const year = useMemo(() => years[yearIndex], [yearIndex])

  const shootingsInCurrentYear = useMemo(
    () => shootingsByPrecinctYear.filter((item) => item.year === year), 
    [year]
  );

  useEffect(() => {
    const newIndex = clamp(Math.floor(progress * years.length), 0, years.length - 1);
    if(newIndex !== yearIndex){
      setYearIndex(newIndex);
    }
  }, [progress, yearIndex])

  return (
      <div className="md:grid md:grid-cols-maptable md:grid-rows-maptable h-full">
        <div className="flex justify-center col-start-2 space-x-8">
          {
            years.map((item, i) => (
              <a 
                key={`year-${item}`} 
                className={`block border-black border mb-4 p-2 text-center hover:cursor-pointer ${item === year ? 'bg-black text-white' : ''}`}  
                onClick={
                  () => {
                    window.scrollTo({top: yearRefs[item].current.getBoundingClientRect().top + window.scrollY, behavior: 'smooth'})}
                }>
                {item}
              </a>
            ))
          }
        </div>
        <div className="col-start-2 row-start-2 row-span-1 h-96 md:h-auto">
          <ParentSize>
            {
              ({width, height}) => <MemoizedMap width={width} height={height} shootingsColorScale={shootingsColorScale} shootingsInCurrentYear={shootingsInCurrentYear} />
            }
          </ParentSize>
        </div>
        <div className="col-start-2 row-start-3 place-self-center">
          <LegendGradient scale={shootingsColorScale} width={200} height={20} />
        </div>
        <div className="hidden md:block row-span-2">
          <table>
            <tbody>
            {
              shootingsInCurrentYear.map((datum, index) => (
                <tr key={`tr-${year}-${index}`}>
                  <td>
                    {datum.precinctName}
                  </td>
                  <td>
                    {datum.shootings}
                  </td>
                </tr>
              ))
            }
            </tbody>
          </table>
        </div>
      </div>
  );
}

export default ShootingsMap;