import { scaleOrdinal } from '@visx/scale';
import { LegendOrdinal } from '@visx/legend';
import ParentSize from '@visx/responsive/lib/components/ParentSize';
import DotMatrix from './DotMatrix';
import { useMemo } from 'react';
import { getProgressBetweenPoints } from '../lib';

var dataset = [
  {group: 'Female', category: 'White', count: 0.0},
  {group: 'Female', category: 'Asian', count: 0.0},
  {group: 'Female', category: 'Hispanic', count: 16.5},
  {group: 'Female', category: 'Black', count: 29.5},
  {group: 'Male', category: 'White', count: 4.2},
  {group: 'Male', category: 'Asian', count: 7.7},
  {group: 'Male', category: 'Hispanic', count: 95.5},
  {group: 'Male', category: 'Black', count: 371.5},
];

const uniqueCategories = dataset.reduce((acc, curr) => {
  if(acc.indexOf(curr.category) === -1){
    acc.push(curr.category);
  }
  return acc;
}, []);

const uniqueGroups = dataset.reduce((acc, curr) => {
  if(acc.indexOf(curr.group) === -1){
    acc.push(curr.group);
  }
  return acc;
}, []);

const totalCount = dataset.reduce((acc, curr) => {
  acc += curr.count;
  return acc;
}, 0);

const groupedData = uniqueGroups.map((group) => {
  return {
    groupName: group,
    data: dataset.filter((datum) => datum.group === group)
  }
});


/**
 * Dense and somewhat complicated logic to synchronize the category highlighting with
 * the dot highlighting.
 * 
 * 1) sort data by category
 * 2) get totals for each category
 * 3) add all prior category totals to each successive total in the array to get an array of "start points"
 * 4) access the array of start points by the index of the category  in uniqueCategories
 */
// const sum = (a, b) => a + b;
// const totalVictims = dataset.reduce((acc, item) => acc + item.count, 0);
// const totalsByCategory = uniqueCategories
//   .map((category) =>  dataset.filter((item) => item.category === category))
//   .map((item2) => (item2.reduce((acc, item) => {return item.count + acc}, 0)));
// const highlightPoints = totalsByCategory.map((item, i) => totalsByCategory.slice(0, i).reduce(sum, 0) / totalVictims);

// const getIsActiveCategory = (category, progress) => highlightPoints[uniqueCategories.indexOf(category)] < progress;

/**
 * Layout stuff
 */
const margin = {top: 20, bottom: 0, left: 0, right: 0};

/**
 * For a legend that runs alongside the chart
 * Currently, it is paced underneath
 */
const colorswatchWidth = 10;
const labelColumnWidth = 16;
const legendGutter = 5;
const legendPadding = 16;
const labelGutter = 4;


/**
 * This is being used for the legend. Could be replaced.
 */
const x = d => d.category;

const colorScale = scaleOrdinal({
  range: ['#cea9be', '#e7466d', '#dde44c', '#ff7c53'],
  domain: dataset.map(x), // alternatively uniqueGroups
});

const gridHeight = 24; // in dots
const gridWidth = 16;

const ExampleDotMatrix = function({progress, width, height}) {
  /**
   * Layout stuff
   */
  const xMax = width  - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;
  const availableWidth = xMax - colorswatchWidth - labelColumnWidth - legendGutter - legendPadding - labelGutter;

  // const activeCategories = useMemo(() => {
  //   return uniqueCategories.filter((category) => getIsActiveCategory(category, progress));
  // }, [progress]);

  const activeIndex = useMemo(() => Math.floor(getProgressBetweenPoints(progress, 0.2, 0.8) * gridHeight * gridWidth), [progress]);

  return (
    <>
      <div className="flex basis-3/4 items-start h-96">
      {
        groupedData.map((group) => {
          const keyName = group.groupName.toLowerCase().split(' ').join('-');
          return <div key={`dot-matrix-container-${keyName}`} className='basis-1/2 h-full'>
            <h4 className="mb-2 text-sm">{group.groupName}</h4>
            <ParentSize>
             {
              ({width, height}) => <DotMatrix 
                data={group.data}
                total={totalCount}
                width={width}
                height={height}
                gridWidth={gridWidth}
                gridHeight={gridHeight}
                colorScale={colorScale}
                activeIndex={activeIndex}
                accessor="count"
                categoryAccessor="category"
              />
             }
            </ParentSize>
          </div>
        })
      }
      </div>
      <LegendOrdinal direction={(window.innerWidth > 640) ? 'column' : 'row'} scale={colorScale}  labelFormat={(label) => `${label}`}>
        {(labels) => (
          <div className="mt-6">
            <div style={{ fontSize: `16px`, paddingLeft: `${legendPadding}px` }} className="basis-1/4 pr-16">
              {labels.map((label, i) => (
                    <div
                      key={`legend-label-${label.text}`} 
                      className='px-2 my-2'
                      style={{ 
                        boxSizing: 'border-box', 
                        borderLeft: `12px solid ${label.value}`,
                        transition: 'background-color .5s'
                      }}
                    >
                      {label.text}
                    </div>

              ))}
            </div>
          </div>
        )}
      </LegendOrdinal>
    </>
  );
}

export default ExampleDotMatrix;