import { LinearGradient } from '@visx/gradient';

const LegendGradient = ({scale, width, height, textFormatter, fontSize=12}) => {
  const range = scale.range();
  const domain = scale.domain();
  return (
    <svg width={width} height={height + (fontSize * 1.666667)}>
      <LinearGradient from={range[0]} to={range[1]} id="myGradient" vertical={false} />
      <rect width={width} height={height} fill="url(#myGradient)" />
      <text y={height} dy={height} fontSize={fontSize}>
        {textFormatter ? textFormatter(domain[0]) : domain[0]}
      </text>
      <text y={height} dy={height} x={width} fontSize={fontSize} textAnchor="end">
        {textFormatter ? textFormatter(domain[1]) : domain[1]}
      </text>
    </svg>
  );
}

export default LegendGradient;